import { Swiper, SwiperSlide } from 'vue-awesome-swiper'   
export default {
  name: "Ment",
  components: { 
    Swiper, 
    SwiperSlide,
  },
  props: {
    showMent: {
      type: Boolean,
      default: false
    },
    MentList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      visables: this.showMent,
      list: this.MentList.map(item => {
        item.selected = false;
        return item
      }),
      selectedList: [],
      swiperOption: {
        initialSlide: 0,
        slidesPerView : 2.4,  
        direction: 'horizontal',  
        loop: false,  
      }
    }
  },
  watch: {
    showMent(n) {
      this.visables = n;
    },
  },
  methods: {
    selected(tokenId) {
      this.list = this.list.map(item => {
        if (item.tokenId == tokenId.tokenId) {
          item.selected = !item.selected;
        }
        return item
      })
      this.selectedList = this.list.filter(item => {
        return item.selected == true
      }).map(item => {
        return item.tokenId
      })
    },
    close() {
      this.$emit("closeMent", false);
    },
    MentToken(tokens) {
      if (this.list.length == 1) {
        this.selected(this.list[0])
        this.$emit("closeMent", this.selectedList)
      } else if (tokens.length > 0) {
        console.log({tokens})
        this.$emit("closeMent", tokens)
      } else {
        this.$toast.fail(this.$t('polygon.mentAlert'));
      }
    }
  },
  computed: {
    MATIC() {
      return this.selectedList.length * 100;
    }
  }
}

